body {
    font-family: "Poppins", sans-serif;
   }
   
   ul {
    list-style: none;
    padding: 0 !important;
    margin: 0 !important;
   }
   
   // home icon
   @font-face {
    font-family: "icomoon";
    src: url("../fonts/icomoon.eot?tjsvhj");
    src: url("../fonts/icomoon.eot?tjsvhj#iefix") format("embedded-opentype"),
     url("../fonts/icomoon.ttf?tjsvhj") format("truetype"),
     url("../fonts/icomoon.woff?tjsvhj") format("woff"),
     url("../fonts/icomoon.svg?tjsvhj#icomoon") format("svg");
    font-weight: normal;
    font-style: normal;
    font-display: block;
   }
   
   [class^="icon-"],
   [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
   
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
   }
   
   .icon-noun_Home_2102808:before {
    content: "\e900";
    color: #b2b2b2;
   }
   
   /************************************/
   .breadcrumb-item + .breadcrumb-item::before,
   .breadcrumb-item span {
    color: #b2b2b2 !important;
   }
   
   // page style
   .filters {
    //  position: sticky;
    //  top: 0;
   
    .filter {
     border: 1px solid #e6e6e6;
     padding: 0rem 1rem 0 1rem;
     margin-bottom: 0.75rem;
   
     .filter-header {
      display: flex;
      justify-content: space-between;
      padding: 1rem 0;
      font-size: 12px;
      cursor: pointer;
   
      .filter-title {
       text-transform: uppercase;
       letter-spacing: 1px;
      }
   
      .filter-icon {
       .down-icon {
        display: none;
       }
   
       .up-icon.show,
       .down-icon.show {
        display: block;
       }
   
       .up-icon.hide,
       .down-icon.hide {
        display: none;
       }
      }
     }
    }
   
    .filter-content {
     padding: 0;
     height: 0;
     overflow: hidden;
     transition: 0.3s all ease;
   
     &.show {
      padding: 1.5rem 0;
      height: auto;
     }
   
     .type {
      display: flex;
      align-items: center;
      margin-bottom: 1rem;
      font-size: 14px;
   
      &:last-child {
       margin-bottom: 0;
      }
   
      .custom-checkbox-wrapper {
       display: inline-block;
       width: 25px;
       height: 25px;
       margin-right: 0.75rem;
       border: 1px solid #e6e6e6;
       position: relative;
   
       span {
        background-color: #000;
        color: #fff;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        text-align: center;
        line-height: 25px;
        cursor: pointer;
        z-index: -1;
        display: none;
       }
   
       span.show {
        display: block;
       }
      }
   
      .type-name {
       text-transform: capitalize;
       margin-right: 0.25rem;
      }
   
      .type-count {
       color: $color;
      }
     }
   
     .price-range {
      display: flex;
      justify-content: space-between;
      margin-bottom: 1rem;
   
      span {
       background-color: #f4f4f4;
       font-size: 11px;
       text-transform: uppercase;
       padding: 0.25rem 0.5rem;
      }
     }
   
     .input-range-wrapper {
      display: flex;
      justify-content: center;
      width: 100%;
      margin: 10px auto;
      margin-top: 1.5rem;
      position: relative;
   
      &::before {
       content: "";
       position: absolute;
       width: 20px;
       height: 20px;
       background-color: #fff;
       border-radius: 50%;
       border: 3px solid #000;
       top: -9px;
       left: 10px;
      }
   
      input[type="range"] {
       -webkit-appearance: none;
       width: 85%;
       height: 3px;
       background-color: #000;
       border-radius: 5px;
       cursor: pointer;
   
       &::-webkit-slider-thumb {
        -webkit-appearance: none;
        width: 20px;
        height: 20px;
        background-color: #fff;
        border-radius: 50%;
        border: 3px solid #000;
       }
      }
     }
   
     .sizes {
      display: flex;
      justify-content: space-between;
   
      .size {
       font-size: 13px;
       text-transform: uppercase;
       border: 1px solid #e6e6e6;
       padding: 0.75rem;
       flex: 0 0 calc(100% / 6);
       text-align: center;
       cursor: pointer;
      }
     }
   
     .colors {
      display: flex;
      justify-content: space-between;
   
      .color {
       border: 1px solid #e6e6e6;
       padding: 0.5rem;
       flex: 0 0 calc(100% / 6);
       cursor: pointer;
   
       span {
        display: inline-block;
        width: 100%;
        height: 100%;
        border: 1px solid #e6e6e6;
       }
   
       &:first-child span {
        background-color: #000;
       }
   
       &:nth-child(2) span {
        background-color: #6f3e18;
       }
   
       &:nth-child(3) span {
        background-color: #d4be8d;
       }
   
       &:nth-child(4) span {
        background-color: #838383;
       }
   
       &:nth-child(5) span {
        background-color: #fff;
       }
   
       &:nth-child(6) span {
        background-color: #0f73ad;
       }
      }
     }
    }
   }
   
   .products-control {
    .products-title {
     text-transform: capitalize;
     font-size: 24px;
   
     .products-count {
      color: #b2b2b2;
     }
    }
   
    .controls {
     font-size: 13px;
     text-transform: capitalize;
   
     span {
      margin-right: 33px;
     }
   
     .show-count {
      margin-right: 42px;
      .custom-select {
       width: 68px;
      }
     }
   
     .sort {
      .custom-select {
       width: 138px;
      }
     }
   
     .custom-select {
      border-top-right-radius: 50px;
      border-top-left-radius: 50px;
      border-bottom-right-radius: 50px;
      border-bottom-right-radius: 50px;
      display: flex;
      position: relative;
   
      &::before {
       display: inline-block;
       content: "\f078";
       font-family: "Font Awesome 5 Free";
       font-weight: 400;
       font-style: normal;
       font-variant: normal;
       text-rendering: auto;
       -webkit-font-smoothing: antialiased;
       width: 15px;
       height: 15px;
       line-height: 15px;
       text-align: center;
       position: absolute;
       top: 15px;
       right: 7px;
      }
   
      select {
       appearance: none;
       outline: none;
       width: 100%;
       padding: 13px 16px 14px 20px;
       border: solid 1px #d8d8d8;
       border-top-left-radius: 50px;
       border-top-right-radius: 50px;
       border-bottom-left-radius: 50px;
       border-bottom-right-radius: 50px;
       background-color: transparent;
       z-index: 2;
       cursor: pointer;
       &:focus {
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
        border-bottom: 0;
       }
      }
     }
    }
   
    .filters-icon {
     display: none;
    }
   
    .hide-controls {
     display: none !important;
    }
   }
   
   //product style
   .product {
    position: relative;
    background-color: turquoise;
   
    .favorite-icon {
     width: 35px;
     height: 35px;
     line-height: 35px;
     text-align: center;
     font-size: 18px;
     background-color: #fff;
     border-radius: 50%;
     position: absolute;
     top: 20px;
     right: 20px;
     transition: 0.3s all ease;
     cursor: pointer;
   
     &:hover {
      background-color: #fbb03b;
      color: #fff;
     }
    }
   
    .product-img {
     img {
      width: 100%;
     }
    }
   
    .product-details {
     background-color: #fff;
     position: absolute;
     width: 100%;
     left: 0;
     bottom: 0;
     padding: 1.25rem 0;
   
     h6 {
      font-size: 14px;
      font-weight: 300;
     }
   
     .price {
      font-size: 16px;
      font-weight: 600;
   
      .discounted-price {
       color: #ff0000;
      }
   
      .original-price {
       color: #cecece;
      }
     }
    }
   }
   
   .pagination {
    padding: 2.5rem 0;
    display: flex;
    justify-content: space-between;
   
    button {
     border: 0;
     background-color: transparent;
    }
   
    .prev,
    .next {
     font-size: 13px;
     text-transform: uppercase;
     text-decoration: underline;
    }
   
    .page-num {
     border: 1px solid #e6e6e6;
     width: 40px;
     height: 40px;
     margin: 0 0.5rem;
    }
   }
   
   .newsletter {
    background-image: url("../../images/blake-wisz-GFrBMipOd_E-unsplash.png");
    padding: 3rem;
   
    .newsletter-text {
     font-size: 26px;
     color: #fff;
    }
   
    .newsletter-form {
     input,
     button {
      border: 0;
      padding: 0.5rem 0.75rem;
      border-radius: 30px;
      font-size: 13px;
     }
   
     input {
      width: 315px;
      margin-right: 1.25rem;
   
      &::placeholder {
       font-size: 13px;
      }
     }
   
     button {
      background-color: #fbb03b;
      text-transform: uppercase;
     }
    }
   }
   
   //media query
   @media (max-width: 768px) {
    .products-control {
     flex-direction: column;
   
     .products-title {
      align-self: flex-start;
      margin-bottom: 1rem !important;
      width: 100%;
      display: flex;
      justify-content: space-between;
   
      .filters-icon {
       display: block;
       font-size: 16px;
       cursor: pointer;
      }
     }
    }
   
    .newsletter-text {
     font-size: 20px !important;
     margin-bottom: 1rem;
    }
   }
   
   // popups styles
   .popup-overlay {
    background-color: rgba(0, 0, 0, 0.2);
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 9999;
    display: none;
    overflow: auto;
    .login-popup {
     height: 628px;
    }
   
    .popup {
     width: 30%;
     background-color: #fff;
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     padding: 0 45px;
     padding-top: 64px;
     margin: 0 auto;
   
     .close-icon {
      width: 13px;
      height: 13px;
      line-height: 13px;
      text-align: center;
      font-size: 13px;
      position: absolute;
      top: 38px;
      right: 38px;
      cursor: pointer;
     }
   
     .popup-heading {
      margin-bottom: 40px;
      .popup-title {
       font-size: 22px;
       text-align: center;
       margin-bottom: 26px;
      }
   
      .popup-desc {
       font-size: 13px;
       color: #7d7d7d;
       text-align: center;
      }
     }
   
     .input-control {
      // background-color: red;
      position: relative;
      margin-bottom: 15px;
   
      input {
       width: 100%;
       height: 45px;
       border-radius: 40px;
       border: solid 1px #d8d8d8;
       padding: 10px 30px;
       font-size: 13px;
       outline: 0;
   
       &::placeholder {
        color: #808080;
        font-size: 13px;
       }
      }
   
      input[type="password"] {
       padding-right: 50px;
      }
   
      .password-icon {
       position: absolute;
       top: 20px;
       right: 27px;
       color: #808080;
       font-size: 13px;
       cursor: pointer;
   
       .hide {
        display: none;
       }
      }
   
      input[type="submit"] {
       background-color: #fbb03b;
       font-size: 13px;
      }
     }
   
     .inner-wrapper {
      display: flex;
      justify-content: space-between;
      margin-bottom: 35px;
   
      .left-side {
       display: flex;
       align-items: center;
       label {
        font-size: 13px;
        margin-left: 1rem;
       }
      }
   
      .right-side {
       span {
        background-color: transparent;
        border: none;
        outline: none;
        font-size: 13px;
        color: #808080;
        cursor: pointer;
       }
      }
     }
   
     .external-login {
      margin-bottom: 20px !important;
      a {
       width: 167px;
       padding: 0.8rem 0rem;
       border-radius: 40px;
       text-decoration: none;
       text-transform: capitalize;
       text-align: center;
       display: flex;
       justify-content: center;
       align-items: center;
   
       i {
        font-size: 18px;
       }
   
       span {
        display: block;
        margin-left: 16px;
        font-size: 13px;
       }
      }
      & > a:nth-child(1) {
       background-color: #3b5998;
       color: #fff;
      }
   
      & > a:nth-child(2) {
       //  background-color: #3b5998;
       border: solid 1px #f1584d;
       color: #000;
      }
     }
    }
   
    .signup-btn-wrapper {
     display: flex;
     justify-content: space-between;
     align-items: center;
     font-size: 13px;
     margin-top: 35px;
   
     .signup-btn {
      cursor: pointer;
     }
    }
   
    .login-btn-wrapper {
     font-size: 13px;
     margin-top: 22px;
     margin-bottom: 34px;
     text-align: center;
   
     .login-btn {
      cursor: pointer;
     }
    }
   
    .forget-password-popup {
     //   display: none;
    }
   
    .interval {
     height: 1px;
     background-color: #d8d8d8;
     margin: 30px 0;
     position: relative;
   
     &::before {
      content: "OR";
      color: #d8d8d8;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #fff;
      padding: 0 1rem;
      font-size: 13px;
     }
    }
   }
   
   @media (max-width: 991px) {
    .popup {
     width: 50% !important;
     margin: 0 auto;
    }
   }
   
   @media (max-width: 767px) {
    .popup {
     width: 60% !important;
    }
   }
   
   @media (max-width: 500px) {
    .popup {
     width: 90% !important;
     padding: 0 20px !important;
     padding-top: 64px !important;
    }
   }